.dark {
  mix-blend-mode: difference;
}
.contentWrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 1%;
  width: 100%;
}
.contentItemWrapper {
  width: 100%;
  margin-top: 1%;
  background: #fff;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
