.dark {
  mix-blend-mode: inherit;
}
.subheader {
  transition: all 1000ms ease-in;
  top: 70px;
  right: 0;
  left: 0;
  position: sticky;
  color: #272727;
  background-color: #f3f2f2;
  // transition: width 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  width: 100%;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  flex-direction: row;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  height: 70px;
  z-index: 1201;
  

  &.hide{
    visibility: hidden;
    height: 0;
    opacity: 0;
    transition: visibility 0.4s, opacity 0.4s linear, height 600ms;
  }
  a {
    padding-top: 5px;
  }

  .container {
    transition: all 400ms ease-in;
    flex-grow: 1;
    height: 100%;
    border-left: #e5e5e5 solid 2px;
    display: flex;
    align-items: center;
    padding: 1%;
    justify-content: space-between;
  }

  .iconWrapper {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
  }

  .iconWrapper svg {
    margin-right: 4%;
  }
}

h3 {
  width: 230px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 2%;
}
@media only screen and (max-width: 768px) {
  .subheader {
    .container {
      padding: 2%;
      font-weight: bold;
    }
  }
  h3 {
    display: none;
  }
}
